import React from "react";
import Img1 from "../img/img1.png";
import Img2 from "../img/img2.png";
import Img3 from "../img/img3.png";
import "./style.css";

export default class Game extends React.Component {

    state = {
        player : 3,
        computer: 3,
        result: " "
    } 

    change = (n) => {

        let no=Math.floor(Math.random()*3)+1;
        this.setState({
            player: n,
            computer: no
        })
        this.props.result(n,no)
        if(this.props.r==4) {
            if(this.props.p > this.props.c) {
                this.setState({
                    result: "you won!"
                })
            }
            else if(this.props.p < this.props.c) {
                this.setState({
                    result: "Try Your Luck Again!"
                })
            }
            else if(this.props.p == this.props.c) {
                this.setState({
                    result: "Tie"
                })
            }

         } else{
            this.setState({
                result: " "
            })
        }
    }

     refreshPage = () => {
        window.location.reload(false);
      }

    render() {
        return(
            <div>
            <div className="row">
            <div className="col-sm-4">
            <img src= {require(`../img/img${this.state.player}.png`)} />
            <h1>Player</h1>
            </div>
            <div className="col-sm-4">
            <h1>{this.state.result}</h1>
            {this.props.r==5 ? <button className="btn btn-outline-danger" onClick={this.refreshPage}>Play again</button> : null }
            </div>
            <div className="col-sm-4">
            <img src={require(`../img/img${this.state.computer}.png`)} />
            <h1>Computer</h1>
            </div>
            </div>
            <div className="options">
            <img src={Img1} onClick={() => this.change(1) }/>
            <img src={Img2} onClick={() => this.change(2) }/>
            <img src={Img3} onClick={() => this.change(3) }/>
            </div>
            <div>
            <h5>Select your Weapon</h5>
            </div>
            </div>
        );
    }
}